.welcome-back {
  position: relative;
  font-weight: 600;
}
.welcome-back-please {
  position: relative;
  font-size: var(--size-16-size-16-semibold-size);
  color: var(--text-color-text-3);
}
.titel {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 8px;
  font-size: 30px;
}
.email {
  position: relative;
  font-weight: 500;
}
.email-wrapper,
.input-child {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.email-wrapper {
  align-self: stretch;
  padding: var(--padding-3xs) var(--padding-3xs) var(--padding-3xs) 0;
  align-items: flex-start;
}
.input-child {
  border: 1px solid var(--darkish-color-dark-2);
  font-weight: 500;
  font-family: var(--size-16-size-16-semibold);
  font-size: var(--size-14-size-14-medium-size);
  background-color: transparent;
  border-radius: var(--br-3xs);
  box-sizing: border-box;
  width: 404px;
  padding: var(--padding-mini) var(--padding-6xl) var(--padding-base)
    var(--padding-xl);
  align-items: center;
}
.input,
.password-wrapper {
  width: 404px;
  display: flex;
  align-items: flex-start;
}
.input {
  flex: 1;
  flex-direction: column;
  justify-content: center;
}
.password-wrapper {
  flex-direction: row;
  padding: var(--padding-3xs) var(--padding-3xs) var(--padding-3xs) 0;
  box-sizing: border-box;
  justify-content: flex-start;
}
.elips-icon {
  position: relative;
  width: 65px;
  height: 5px;
}
.elips-wrapper {
  border-radius: var(--br-3xs);
  border: 1px solid var(--darkish-color-dark-2);
  box-sizing: border-box;
  width: 404px;
  height: 48px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-mini) var(--padding-6xl) var(--padding-base)
    var(--padding-xl);
  align-items: center;
  justify-content: flex-start;
}
.input-parent,
.input1 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.input1 {
  width: 404px;
  justify-content: center;
}
.input-parent {
  height: 175px;
  justify-content: flex-start;
  gap: 5px;
}
.remember-child {
  cursor: pointer;
  position: relative;
  border-radius: 3px;
  border: 1px solid var(--text-color-text-3);
  box-sizing: border-box;
  width: 16px;
  height: 16px;
}
.remember {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 9px;
}
.forgot-password {
  position: relative;
  font-weight: 500;
  text-align: right;
}
.remember-parent {
  width: 361px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.main {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 20px;
}
.sign-in2 {
  position: relative;
  font-size: var(--size-16-size-16-semibold-size);
  font-weight: 600;
  font-family: var(--size-16-size-16-semibold);
  color: var(--text-color-text-1);
  text-align: center;
}
.button {
  cursor: pointer;
  border: 0;
  padding: 14px var(--padding-xl);
  background-color: var(--primary-color);
  border-radius: var(--br-3xs);
  width: 404px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.icongoogle {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.sign-in-with {
  position: relative;
  font-size: var(--size-16-size-16-semibold-size);
  font-weight: 600;
  font-family: var(--size-16-size-16-semibold);
  color: var(--text-color-text-3);
  text-align: left;
}
.button1 {
  cursor: pointer;
  border: 1px solid var(--darkish-color-dark-2);
  padding: 13px;
  background-color: transparent;
  border-radius: var(--br-3xs);
  box-sizing: border-box;
  width: 404px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: var(--gap-3xs);
}
.button1:hover {
  border: 1px solid #6456e8;
  box-sizing: border-box;
}
.buttons {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 15px;
}
.span {
  color: var(--text-color-text-1);
}
.sign-up-for {
  font-weight: 500;
  color: var(--text-color-pure-white);
}
.dont-have-an-container,
.frame-child {
  position: relative;
}
.frame-child {
  width: 97.87px;
  height: 13.68px;
}
.dont-have-an-account-sign-up-parent {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
  gap: var(--gap-3xs);
}
.bottom,
.sign-in1 {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: var(--gap-6xl);
}
.bottom {
  align-items: center;
  color: #929eae;
}
.sign-in1 {
  position: absolute;
  top: 199px;
  left: 135px;
  align-items: flex-start;
}
.image {
  position: absolute;
  top: 0;
  left: 765px;
  background-color: rgba(27, 33, 45, 0.1);
  width: 675px;
  height: 900px;
}
.exclude-icon,
.maglo {
  position: relative;
  width: 30px;
  height: 30px;
}
.maglo {
  display: inline-block;
  width: 80px;
  height: 23px;
  flex-shrink: 0;
}
.logo {
  position: absolute;
  top: 40px;
  left: 135px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 12px;
  font-size: 18px;
  font-family: var(--font-gordita);
}
.sign-in {
  position: relative;
  background-color: var(--darkish-color-dark-bg);
  width: 100%;
  height: 900px;
  overflow: hidden;
  text-align: left;
  font-size: var(--size-14-size-14-medium-size);
  color: var(--text-color-pure-white);
  font-family: var(--size-16-size-16-semibold);
}
@media screen and (max-width: 420px) {
  .remember-parent {
    flex-direction: row;
    gap: 76px;
    align-items: flex-start;
    justify-content: flex-start;
  }
}
